@import url(https://fonts.googleapis.com/css?family=Oxygen+Mono|Oxygen:300,400,700);
html, body {
  min-height: 100%;
  margin: 0;
  padding: 0;
}
body {
  font-family: "Oxygen", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #e4b7e5;
  background-color: #4a4063;
}
pre, code, .monospace, .code {
  font-family: "Oxygen Mono", monospace;
  color: #42b883;
  background-color: #35495e;
}
code {
  padding: .25rem .5rem;
  display: inline-block;
  border-radius: 2px;
}
h1, .h1, h2, .h2 {
  font-weight: lighter;
  margin-top: 0;
}
h1, .h1 {
  font-size: 3rem;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b883;
}
.app {
  text-align: center;
}
.img-fluid {
  max-width: 100%;
  height: auto;
}
.svg-icon {
  display: inline-block;
}
.svg-icon svg {
    overflow: hidden;
    width: 20px;
    height: 20px;
    fill: #42b883;
    display: inline-block;
    vertical-align: middle;
}
.footer {
  padding: 10vh 16px;
}

